/* xclone_project/frontend/src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* グローバルリセット */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* HTML, Body のスタイル */
html, body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
      Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    background-color: #f0f0f0;
    color: #333;
    height: 100%;
    width: 100%;
    line-height: 1.6;
    overflow-x: hidden;
}

/* ルート要素 (Reactアプリが描画される場所) */
#root {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    padding: 20px;
}

/* ボタンのベーシックスタイル */
button {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 5px;
    background-color: #007bff;
    color: white;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #0056b3;
}

/* リンクのスタイル */
a {
    color: #007bff;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}

/* レスポンシブ対応 */
@media (max-width: 768px) {
    body {
        font-size: 14px;
    }

    button {
        font-size: 0.875rem;
        padding: 0.4rem 0.8rem;
    }
}
